const render = function render (component, node) {
	component.$parent = node;
	component._render();
}

const rCreate = function rCreate (type, props = {}, ...children) {
	return {type, props: props || {}, children: children || []};
}

const createElement = function createElement (node) {
	if (typeof node == 'string' || typeof node == 'number' || typeof node == 'boolean') {
		return document.createTextNode(node);
	} else if (typeof node.type == 'function') {
		const instance = new node.type(node.props);
		return (typeof instance.isComponent == 'function' &&
			instance.isComponent()) ? createElement(instance.render()) : createElement(instance);
	}


	const $element = document.createElement(node.type);

	setProperties($element, node.props);

	node.children
		.map(createElement)
		.map($element.appendChild.bind($element));

	return $element;
}; 

const isEvent = function isEvent(name) {
	if (name == 'onClick' || name == 'onChange') {
		return true;
	}

	return false;
}

const changed = function changed(node1, node2) {
  return typeof node1 !== typeof node2 ||
         (typeof node1 === 'string' || typeof node1 == 'number' || typeof node1 == 'boolean') && node1 !== node2 ||
         node1.type !== node2.type
};

const setProperties = function setProperties ($element, props) {
	Object.keys(props).forEach((key) => {
		setProperty($element, key, props[key]);
	});
};

const setProperty = function setProperty ($element, name, value) {
	if (name === 'className') {
		$element.setAttribute('class', value);
	} else if (isEvent(name)) {
		addEventListener($element, name, value);
		return;
	}else {

		$element.setAttribute(name, value);
	}
};

const addEventListener = function addEventListener ($element, name, handler) {
	$element.addEventListener(getActionNameFromKey(name), handler);
};

const getActionNameFromKey = function getActionNameFromKey (key) {
	if (key === 'onClick') {
		return 'click';
	} else if (key === 'onChange') {
		return 'input';
	}
}

const updateElement = function updateElement ($parent, newNode, oldNode, index = 0) {
	if (oldNode == undefined) {
		$parent.appendChild(
			createElement(newNode)
		);

	} else if (newNode == undefined) {
		$parent.removeChild(
      $parent.childNodes[index]
    );
	} else if (changed(newNode, oldNode)) {

    $parent.replaceChild(
      createElement(newNode),
      $parent.childNodes[index]
    );

  } else if (newNode.type) {
    const newLength = newNode.children.length;
    const oldLength = oldNode.children.length;

    for (let i = 0; i < newLength || i < oldLength; i++) {
      updateElement(
        $parent.childNodes[index],
        newNode.children[i],
        oldNode.children[i],  
        i
      );
    }

    updateAttributes($parent.childNodes[index], newNode, oldNode);
  }
};

const updateAttributes = function updateAttributes ($element, newNode, oldNode) {

	Object.keys(oldNode.props).forEach((key) => {
		if(!newNode.props[key]) {
			$element.removeAttribute(key);
		}
	});

	Object.keys(newNode.props).forEach((key) => {
		if(newNode.props[key] != oldNode.props[key]) {
			$element.setAttribute(key, newNode.props[key]);
		}
	});
};

export {
	render,
	rCreate,
	updateElement
};


