import RComponent from './RComponent';
import {rCreate} from './VirtualDOM';

class Hello extends RComponent {
	constructor (props) {
		super(props);
	}

	render () {
		const name = this.props.name;
		return (
			<div>
				Hello {name}
			</div>
		);
	}
}


export default Hello;


