import RComponent from './RComponent';
import Hello from './Hello';
import {rCreate, render} from './VirtualDOM';

class Button extends RComponent {
	constructor (props) {
		super(props);
		this.state = {
			name: 'mohamed',
			count: 0
		};
		document.Button = this;
	}

	handleInputChange(e) {
		this.setState({
			name: e.target.value
		})
	}

	render () {
		const handleInputChange = this.handleInputChange.bind(this);
		return (
			<div>
				{this.state.name}
				<input onChange={handleInputChange}/>
				<Hello name={this.state.name} />
				<Hi />
				<button className="btn" text={this.state.name}>{this.state.name}</button>
				<b>{this.state.count}</b>
			</div>
		);
	}
}

function Hi() {
	return (
		<a href="#">|La La</a>
	);
}

let btn = new Button({});
const $root = document.getElementById('app');

render(btn, $root);

export default {};


